import React, { createContext, useContext, useRef, useState, useEffect } from 'react';
import { 
    listenerOptions, 
    frameDomain
} from '../consts';
import { validateTeConnect } from '../utils';

const teConnectCtx = createContext(null);

const TEConnect = ({ children, teConnect }) => {
    const validatedTeConnect = validateTeConnect(teConnect);

    const frameRef = useRef(null);
    const prCtrlRef = useRef(null);

    const [ ctx, setCtx ] = useState(() => ({
        ...validatedTeConnect,
        frameRef: frameRef,
        prCtrlRef: prCtrlRef
    }));

    const { Provider } = teConnectCtx;

    useEffect(() => {
        const listenForId = e => {
            if (e.origin === frameDomain) {
                if (e.data && typeof(e.data) === 'string') {
                    const { formId, prFormId } = JSON.parse(e.data);
                    
                    if (formId && ctx) 
                        setCtx(currentCtx => ({
                            ...currentCtx,
                            formId
                        }));

                    if (prFormId && ctx)
                        setCtx(currentCtx => ({
                            ...currentCtx,
                            prFormId
                        }));
                }
            }
        };

        window.addEventListener('message', listenForId, listenerOptions);
        return () => window.removeEventListener('message', listenForId, listenerOptions);
    }, [ctx, setCtx]);

    return (
        <Provider value={ctx}>
            <>{children}</>
        </Provider>
    );
}

const useTeInstance = _ => useContext(teConnectCtx);

export { TEConnect, useTeInstance };
