import {
    teConnectKeyNames, 
    prInterfaceProps,
    NO_INSTANCE, 
    INCOMPLETE_INSTANCE,
    MISSING_INTERFACE,
    INTERFACE_NOT_INSTANTIATED,
    PR_INTERFACE_MALFORMED,
    defaultApplePayOptions,
    googleApiUrl
} from '../consts'

export const validateTeConnect = teConnectInstance => {
    if (!teConnectInstance)
        throw new Error(NO_INSTANCE);

    if (typeof(teConnectInstance.requestClient) !== 'object')
        throw new Error(INCOMPLETE_INSTANCE);
    
    const { requestClient } = teConnectInstance;

    if (typeof(requestClient.hasOwnProperty) !== 'function')
        throw new Error(INCOMPLETE_INSTANCE);

    for(const keyName of teConnectKeyNames) {
        if (!requestClient.hasOwnProperty(keyName)) {
            throw new Error(INCOMPLETE_INSTANCE);
        }
    }

    return teConnectInstance;
}

export const validatePrInterface = prInterface => {
    if (!prInterface)
        throw new Error(MISSING_INTERFACE);

    if (typeof(prInterface) === 'function')
        throw new Error(INTERFACE_NOT_INSTANTIATED);

    for (const interfaceProp of prInterfaceProps)
        if (typeof(prInterface[interfaceProp]) !== 'function')
            throw new Error(PR_INTERFACE_MALFORMED)

    const { createPaymentRequestToken: createPaymentBase, mountGoogleButton } = prInterface;

    return ({ createPaymentBase, mountGoogleButton });
}

const findButtonStyle = selectedStyle => {
    if (typeof(selectedStyle) === 'string') {
        const styleToLower = selectedStyle.toLowerCase();

        switch(styleToLower) {
            case 'black':
                return 'black';
            case 'white':
                return 'white';
            case 'white-outline':
                return 'white-with-line';
            default:
                return 'black';
        }
    }
    else
        return 'black';
}

export const buildApplePayOptions = customerSuppliedOptions => {
    if (customerSuppliedOptions) {
        const { applePayOptions } = customerSuppliedOptions;

        if (applePayOptions && typeof(applePayOptions) === 'object') {
            const { 
                buttonLanguage: chosenLang, 
                buttonType: chosenType, 
                buttonStyle: chosenStyle 
            } = applePayOptions;

            const selectedStyle = findButtonStyle(chosenStyle);

            return {
                buttonLanguage: (chosenLang && typeof(chosenLang) === 'string') ? `${chosenLang}` : defaultApplePayOptions.buttonLanguage,
                buttonType: (chosenType && typeof(chosenType) === 'string') ? `${chosenType}` : defaultApplePayOptions.buttonType,
                buttonStyle: selectedStyle
            }
        }
    }

    return defaultApplePayOptions;
}
