import React, { useState, useCallback } from "react";
import { TEC_APPLE_PAY_BTN_ID, TEC_APPLE_PAY_WRAPPER } from '../consts';
import { validatePrInterface, buildApplePayOptions } from '../utils';


const TecApplePayButton = ({ prInterface, tecPrOptions, teInstanceProps }) => {
    const { createPaymentBase } = validatePrInterface(prInterface);
    const [ applePayOptions ] = useState(() => buildApplePayOptions(tecPrOptions));
    const { 
        prCtrlRef, 
        prFormId, 
        requestClient: { instanceId }
    } = teInstanceProps;

    const createPaymentRequestToken = useCallback(async _ => {
        try {
            await createPaymentBase({
                instanceId,
                formId: prFormId,
                controllerRef: prCtrlRef.current
            });
        }
        catch(e) {
            console.error('[TEConnect Session Error]: ', e);
        }
    }, [instanceId, prFormId, prCtrlRef]);
    
    return (
        <div id={ TEC_APPLE_PAY_WRAPPER }>
            <style>{`
                .apple-pay-button {
                    display: inline-block;
                    -webkit-appearance: -apple-pay-button;
                    -apple-pay-button-type: ${applePayOptions.buttonType};
                }
                .apple-pay-button-black {
                    -apple-pay-button-style: black;
                }
                .apple-pay-button-white {
                    -apple-pay-button-style: white;
                }
                .apple-pay-button-white-with-line {
                    -apple-pay-button-style: white-outline;
                }
            `}
            </style>

            <div 
                id={ TEC_APPLE_PAY_BTN_ID }
                className={ `apple-pay-button apple-pay-button-${applePayOptions.buttonStyle}` }
                onClick={ createPaymentRequestToken }
                lang={ applePayOptions.buttonLanguage }
            ></div>
        </div>
    );
}

export default TecApplePayButton;
