import React, { useEffect, useState, useRef } from 'react';
import TecApplePayButton from './tecApplePay';
import TecGooglePayButton from './tecGooglePay';
import { useTeInstance } from '../teConnectContext';

import { 
    PR_FRAME_ID,
    PR_FRAME_TITLE,
    PR_FRAME_NAME,
    TEC_EVENT_ELEMENT_ID
} from '../consts';


export const useTecPaymentsRequest = _ => {
    const { paymentsRequestInterface } = useTeInstance();

    return paymentsRequestInterface;
}

export const TecPaymentRequestButtons = ({ paymentRequestInterface, tecPrOptions }) => {
    const instanceDetails = useTeInstance();
    const eventElementRef = useRef(null);
    const [ applePayActive, setApplePayActive ] = useState(false);
    const [ googlePayActive, setGooglePayActive ] = useState(false);

    useEffect(() => {
        if (paymentRequestInterface && typeof(paymentRequestInterface.canMakePayments) === 'function') {
            paymentRequestInterface.canMakePayments().then( canMakePaymentsResp => {
                if (canMakePaymentsResp !== null) {
                    const { applePay, googlePay } = canMakePaymentsResp;

                    if (typeof( applePay ) !== 'undefined')
                        setApplePayActive(applePay);
                    else
                        setApplePayActive(false);
    
                    const googleRespType = typeof(googlePay);
                    if (googleRespType !== 'undefined')
                        setGooglePayActive( (googleRespType === 'boolean') ?
                            googlePay : googlePay.result
                        );
                    else
                        setGooglePayActive(false);
                }
            });
        }
    }, [paymentRequestInterface]);
    
    return (
        <>
            <div className={ TEC_EVENT_ELEMENT_ID } ref={ eventElementRef }>
                {applePayActive && 
                    <iframe
                        id={ PR_FRAME_ID }
                        title={ PR_FRAME_TITLE }
                        width="100%"
                        src={ instanceDetails.prFrameUrl }
                        loading="lazy"
                        frameBorder="0"
                        allowtransparency="true"
                        scrolling="no"
                        name={ PR_FRAME_NAME }
                        ref={ instanceDetails.prCtrlRef }
                        style={{ display: 'none', visibility: 'hidden' }}
                    />
                }
            </div>

            {applePayActive &&
                <TecApplePayButton 
                    prInterface={ paymentRequestInterface } 
                    tecPrOptions={ tecPrOptions } 
                    teInstanceProps={ instanceDetails } 
                />
            }

            {googlePayActive &&
                <TecGooglePayButton 
                    prInterface={ paymentRequestInterface } 
                    tecPrOptions={ tecPrOptions } 
                    teInstanceProps={{ ...instanceDetails, eventRef: eventElementRef }} 
                />
            }
        </>
    )
}
    
