import React, { useEffect, useState } from 'react';
import { 
    TecPaymentRequestButtons,
    useTecPaymentsRequest,
    useTeConnect,
    CardEntry
} from '@magensa/te-connect-react';
import { extendedOptionsObject } from '../../consts';


const customStyles = {
    base: {
        wrapper: { 
            margin: 0, 
            padding: 0
        },
        variants: {
            inputType: 'outlined',
            inputMargin: 'dense',
            autoMinHeight: true
        },
       backgroundColor: "#424242"
    },
    boxes: {
        labelColor: "#BB86FC",
        textColor: "#BB86FC",
        borderRadius: 10,
        errorColor: "#CF6679",
        inputColor: '#121212'
    }
}


const hardCodedPaymentRequestObj = {
    storeDisplayName: "TEConnect Dev Store",
    currencyCode: "USD",
    countryCode: "US",
    supportedNetworks: ['visa', 'masterCard', 'amex', 'discover', 'jcb'],
    merchantCapabilities: ['supports3DS'],
    total: {
        label: "Test Transaction",
        amount: "1.00",
        type: "final"
    },
    shippingType: ['shipping'],
    shippingMethods: [
        {    
            label: 'Free Shipping',
            detail: "Arrives in a week",
            amount: '0.00',
            identifier: "FreeShipping"
        },
        {    
            label: 'Not Free Shipping',
            detail: "Arrives in less than week",
            amount: '0.11',
            identifier: "ChargeShipping"
        }
    ],
    requiredShippingContactFields: [
        'name',
        'email',
        'phone',
        'postalAddress'
    ],
    requiredBillingContactFields: [
        'name',
        'email',
        'phone'
    ],
    shippingContact: {
        phoneNumber: '888-8888',
        emailAddress: 'something@example.ne',
        givenName: 'Bob',
        familyName: 'Kahuna',
        addressLines: ['123 Main St.', 'Suite 101'],
        locality: 'Los Angeles',
        administrativeArea: 'CA',
        postalCode: '90010',
        country: 'United States',
        countryCode: 'US',
    },
    billingContact: {
        emailAddress: 'something@example.ne',
        givenName: 'Bob',
        familyName: 'Kahuna',
        addressLines: ['123 Main St.', 'Suite 101'],
        subLocality: 'Los Angeles',
        locality: 'CA',
        postalCode: '90010',
        country: 'United States',
        countryCode: 'US',
    }
};

const payReqPlaceholder = { applePay: hardCodedPaymentRequestObj, googlePay: extendedOptionsObject };

const ExampleApp = () => {
    const [ prInterface, setPrInterface ] = useState(() => false);
    const [ tecResult, setTecResult ] = useState("");
    const [ canMakePaymentsResult, setCanMakePaymentsResult ] = useState("");
    const { createPayment, getCurrentElements } = useTeConnect();
    const createTecPrInterface = useTecPaymentsRequest();
    const [ testHandler, setTestHandler ] = useState(null);

    useEffect(() => {
        if (createTecPrInterface) {
            const tecPrInterface = createTecPrInterface({ applePay: hardCodedPaymentRequestObj });
            console.log(tecPrInterface);
            console.log(typeof(tecPrInterface.createPaymentRequestToken));

            setTestHandler(tecPrInterface.createPaymentRequestToken);

            tecPrInterface.canMakePayments().then( availablePrMethods => {
                console.log('canMakePayments Result: ', availablePrMethods);
                setCanMakePaymentsResult(JSON.stringify(availablePrMethods));

                if (availablePrMethods)
                    setPrInterface(tecPrInterface);
                else
                    setTecResult("Apple Pay not available");
            });

            tecPrInterface.listenFor('confirm-token', async (tokenResp) => {
                console.log('[CUSTOMER TEC COMPLETE EVENT]: ');
                console.log(tokenResp);
                const JsonResp = JSON.stringify(tokenResp.tokenDetails, null, '\t');
                console.log(JsonResp);

                setTecResult(JsonResp);

                if (tokenResp && tokenResp.tokenDetails && tokenResp.tokenDetails.type === 'applePay')
                    tokenResp.completePayment('success');
            });

            tecPrInterface.listenFor('cancel-transaction', async (cancelEvent) => {
                console.log('[CUSTOMER TEC CANCEL EVENT]: ');
                console.log(cancelEvent);
            });

            tecPrInterface.listenFor('shipping-method-update', async (shippingMethodUpdateEvent) => {
                console.log('[CUSTOMER TEC SHIPPING UPDATE EVENT]: ');
                console.log(shippingMethodUpdateEvent);

                const { shippingMethod, completeShippingMethodSelection } = shippingMethodUpdateEvent;

                const notFreeUpdate = {
                    newTotal: {
                        label: "Test Trx with Not Free Shipping",
                        amount: "1.11",
                        type: "final"
                    }
                }

                const freeUpdate = {
                    newTotal: {
                        label: "Test Trx w/ Free Shipping",
                        amount: "1.00",
                        type: "final"
                    }
                }

                const outOfUsShipping = {
                    newTotal: {
                        label: "Test Trx w/ Out of Scope Shipping",
                        amount: "1.23",
                        type: "final"
                    }
                }

                let shippingMethodUpdateObj;
                switch (shippingMethod.identifier) {
                    case "OutOfCountryShip":
                        shippingMethodUpdateObj = outOfUsShipping;
                        break;
                    case 'ChargeShipping':
                        shippingMethodUpdateObj = notFreeUpdate;
                        break;
                    default:
                        shippingMethodUpdateObj = freeUpdate;

                }

                completeShippingMethodSelection(shippingMethodUpdateObj);
            });

            tecPrInterface.listenFor('payment-method-selection', async (paymentMethodEvent) => {
                console.log('[CUSTOMER TEC PAYMENT METHOD EVENT]:');

                const { paymentMethod, completePaymentMethodSelection } = paymentMethodEvent;

                console.log('paymentMethod: ', paymentMethodEvent);

                const hardCodedPaymentUpdateObj = {
                    newTotal: {
                        label: "Test Trx with payment method",
                        amount: "1.01",
                        type: "final"
                    }
                }

                completePaymentMethodSelection(hardCodedPaymentUpdateObj);
            });

            tecPrInterface.listenFor('shipping-contact-update', async (shippingContactEvent) => {
                console.log('[CUSTOMER SHIPPING CONTACT EVENT]:');
                console.log('shippingContactEvent: ', shippingContactEvent);

                const { shippingContact, completeShippingContactSelection } = shippingContactEvent;

                const hardCodedShippingContactUpdateObj = {
                    newTotal: {
                        label: "Test Trx positive shippingContact",
                        amount: "1.02",
                        type: "final"
                    },
                    newShippingMethods: [
                        {    
                            label: 'Outside US Shipping',
                            detail: "Arrives in 7-12 weeks",
                            amount: '100.00',
                            identifier: "OutOfCountryShip"
                        },
                        {    
                            label: 'Not Free Shipping',
                            detail: "Arrives in less than week",
                            amount: '0.11',
                            identifier: "ChargeShipping"
                        }
                    ]
                }

                const errObj = {
                    newTotal: {
                        label: "Test Trx negative shippingContact",
                        amount: "1.03",
                        type: "final"
                    },
                    errors: [
                        {
                            errorType: "failure",
                            message: "Generic Error Message for an unkown error"
                        }
                    ]
                }

                console.log(`[${shippingContact.countryCode}]`);

                completeShippingContactSelection(
                    (shippingContact.countryCode === "US") ? hardCodedShippingContactUpdateObj : errObj
                );
            });
        }
    }, [createTecPrInterface, setTecResult]);

    const clickHandler = async(e) => {
        try {
            const elements = getCurrentElements();
            const teConnectResponse = await createPayment(elements);
            console.log('result:', teConnectResponse);
        }
        catch(err) {
            console.error(err);
        }
    }
    
    return (
        <div style={{ height: '100%' }}>
            <h1>TEC Demo</h1>
            <CardEntry stylesConfig={ customStyles } />
            {/* { prInterface && <TecPaymentRequestButtons paymentRequestInterface={ prInterface } /> } */}

            {testHandler && <button onClick={testHandler}>Test Button</button>}
            
            <button onClick={ clickHandler }>Create Token</button>
            
            {tecResult && 
                <div style={{ 
                    height: '100%', 
                    padding: '3rem', 
                    margin: '3rem',
                    minWidth: '70%', 
                    overflowWrap: 'break-word',
                    whiteSpace: 'pre-wrap'
                }}>
                    <h3>Payment Request Results:</h3>
                    {tecResult}
                </div> 
            }

            {canMakePaymentsResult &&
                <div style={{ 
                    height: '100%', 
                    padding: '3rem', 
                    margin: '3rem',
                    minWidth: '70%', 
                    overflowWrap: 'break-word',
                    whiteSpace: 'pre-wrap'
                }}>
                    <h3>CanMakePaymentsResult:</h3>
                    {canMakePaymentsResult}
                </div> 
            }

        </div>
    )
}

export default ExampleApp;
