import React, { useEffect } from 'react';
import { useTeInstance } from '../teConnectContext';
import { 
    FRAME_ID,
    FRAME_TITLE,
    FRAME_NAME,
    STYLES_TRIGGER,
    framePath,
    defaultFrameStyles
} from '../consts';

const useTeConnect = _ => {
    const { frameUrl, frameRef, formId, createPayment } = useTeInstance();
    const getCurrentElements = _ => ({
        elements: frameRef.current,
        formId
    });

    return {
        frameUrl,
        frameRef,
        createPayment,
        getCurrentElements,
        formId
    }
}

const CardEntry = ({ stylesConfig }) => {
    const { frameUrl, frameRef, formId } = useTeConnect();

    useEffect(() => {
        if (stylesConfig && formId) {
            if (frameRef && frameRef.current) {
                frameRef.current.contentWindow.postMessage(
                    JSON.stringify({
                        type: STYLES_TRIGGER,
                        styles: stylesConfig
                    }), framePath
                );
            }
        }
    }, [ frameRef, formId, stylesConfig ]);

    return (
        <iframe
            id={ FRAME_ID }
            title={ FRAME_TITLE }
            width="100%"
            src={ frameUrl }
            loading="lazy"
            frameBorder="0"
            allowtransparency="true"
            scrolling="no"
            name={ FRAME_NAME }
            ref={ frameRef }
            allowpaymentrequest="true"
            style={ defaultFrameStyles }
        />
    )
}

export {
    useTeConnect,
    CardEntry
}
